.chart-bar{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.chart-bar_inner{
    height: 100px;
    width: 30px;
    border: 1px solid #313131;
border-radius: 12px;
background-color: #c3b4f3;
overflow: hidden;
display: flex;
flex-direction: column;
justify-content: flex-end;
}
.chart-bar_fill{
    background-color: blue;
    width: 100%;
    transition: all 0.3s ease-out;
}
.chart-bar_label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
}